<template>
  <div class="number-line-wrap">
    <span v-for=" digit in digits" :class="digit.val" :key="digit.id">
      <!-- <span></span> -->
    </span>
  </div>
</template>

<script>
import { map, times } from 'lodash'
export default {
  name: 'Score',
  props: {
    score: String
  },
  computed: {
    digits: function() {
      let _score = this.score
      times(8 - _score.length, () => {
        _score = '#' + _score
      })
      return map(_score, (dig, index) => {
        return {
          val: dig !== '.' ? 'number-wrap n-' + dig : 'number-wrap n-dot',
          id: index
        }
      })
    }
  }
}
</script>

