<script>
import { map, pick } from 'lodash'
import axios from 'axios'
import Score from '@/components/Score.vue'
import Gallary from '@/components/Gallary.vue'
import moment from 'moment'
export default {
  name: 'page',
  components: { Score, Gallary },
  data() {
    return {
      type: 70000,
      bonus: {
        moneytree: '0000.00',
        classic: '暂未开放',
        treasure: '暂未开放'
      },
      bonusHitMin: 5000.0,
      bonusHitMax: 10000.0,
      finger: '0',
      user: '0',
      userName: '试玩',
      balance: 0.0,
      token: '0',
      gameList: [],
      apiConfig: {
        list:
          'http://47.88.170.136:3000/gameList/' +
          this.type +
          '?isAll=false&userId=',
        bonus: 'http://testgame.risheng3d.com:45557/getBonusConfig',
        stage: 'develop_without_finger',
        realm: 'ali'
      },
      login: {
        userName: '',
        userPwd: '',
        buId: 0
      }
    }
  },
  created() {
    if (
      // window.location.hostname == '192.168.3.141' ||
      window.location.hostname == 'webpage.risheng3d.com' ||
      window.location.hostname == 'webpage.na77.org'
    ) {
      const search = window.location.search.substring(1)
      let params = { userId: '947117', token: '0' }
      map(search.split('&'), query => {
        if (query.split('=').length === 2) {
          const key = decodeURIComponent(query.split('=')[0])
          const val = decodeURIComponent(query.split('=')[1])
          params[key] = val
        }
      })
      params = pick(params, ['userId', 'token'])
      if (params['userId'] == '0') params['userId'] = '947117'
      this.user = params['userId']
      this.token = params['token']
      this.finger = '0'
      if (
        window.location.hostname == 'webpage.risheng3d.com' //'192.168.3.161'
      )
        this.apiConfig = {
          list: 'http://a1.na12345.com:3000/player/gameList/',
          bonus: 'http://game.risheng3d.com:45557/getBonusConfig',
          realm: 'ali',
          stage: 'prod',
          balanceApi: 'https://n1admin.na12345.com/player/balance/'
        }
      if (window.location.hostname == 'webpage.na77.org')
        this.apiConfig = {
          list: 'http://a1.na12345.com:3000/player/gameList/',
          bonus: 'http://game.risheng3d.com:45557/getBonusConfig',
          realm: 'aws',
          stage: 'prod',
          balanceApi: 'https://n1admin.na12345.com/player/balance/'
        }
    } else {
      const search = window.location.search.substring(1)
      let params = { userId: '0', token: '0', finger: '0' }
      map(search.split('&'), query => {
        if (query.split('=').length === 2) {
          const key = decodeURIComponent(query.split('=')[0])
          const val = decodeURIComponent(query.split('=')[1])
          params[key] = val
        }
      })
      params = pick(params, ['userId', 'token', 'finger'])
      this.user = params['userId']
      this.token = params['token']
      if (params['finger'] !== 'rotta' + moment().format('YYYYMMDD') + 'na') {
        this.finger = '0'
      } else {
        this.finger = params['finger']
      }
      const realm =
        window.location.hostname.indexOf('na77') > -1 ? 'aws' : 'ali'
      this.apiConfig = {
        list:
          'http://47.88.170.136:3000/gameList/' +
          this.type +
          '?isAll=false&userId=',
        bonus: 'http://testgame.risheng3d.com:45557/getBonusConfig',
        realm: realm,
        stage: 'prod',
        balanceApi: 'https://d3rqtlfdd4m9wd.cloudfront.net/player/balance/'
      }
    }
    axios.get(this.apiConfig.list + this.user).then(res => {
      this.gameList = res.data.payload || res.data
    })

    setInterval(() => {
      if (this.token + '' !== '0') {
        axios
          .get(this.apiConfig.balanceApi + this.user, {
            headers: { Authorization: this.token }
          })
          .then(res => {
            if (res.data.code + '' == '0') {
              this.balance = parseFloat(res.data.payload).toFixed(2)
            } else {
              this.user = '0'
              this.token = '0'
              this.finger = '0'
              this.apiConfig.stage = 'prod'
            }
          })
      }
      if (this.type !== 90000)
        axios
          .post(this.apiConfig.bonus, {
            gameType: 'all'
          })
          .then(res => {
            this.bonus.moneytree = (
              (res.data.config[0] &&
                res.data.config[0]['gameType'] === 'moneytree' &&
                res.data.config[0]['bonusPool']) ||
              0.0
            ).toFixed(2)
            this.bonusHitMin = (
              res.data.config[0]['bonusHitMin'] || 0.0
            ).toFixed(2)
            this.bonusHitMax = (
              res.data.config[0]['bonusHitMax'] || 0.0
            ).toFixed(2)
          })
    }, 2000)
  },
  methods: {
    isAuth: function() {
      return this.finger === '0' //|| this.finger === 'test'
    },
    auth: function() {
      axios
        .post(
          'https://xbbw2jfb58.execute-api.ap-southeast-1.amazonaws.com/dev/game/login',
          this.login
        )
        .then(res => {
          if (res.data.code + '' === '0') {
            this.user = res.data.data.userId
            this.userName = res.data.data.username
            this.balance = res.data.data.balance
            this.token = res.data.data.token
            this.apiConfig.stage = this.finger !== '0' ? 'develop' : 'prod'
            this.apiConfig.list =
              this.finger !== '0'
                ? 'http://47.88.170.136:3000/gameList/' +
                  this.type +
                  '?isAll=true&userId='
                : 'http://47.88.170.136:3000/gameList/' +
                  this.type +
                  '?isAll=false&userId='
            axios.get(this.apiConfig.list + this.user).then(res => {
              this.gameList = res.data.payload || res.data
              this.finger = '0'
            })
          } else {
            this.user = '0'
            this.userName = '试玩'
            this.balance = 0.0
            this.token = '0'
            this.finger = '0'
            this.apiConfig.stage = 'prod'
            this.apiConfig.list =
              'http://47.88.170.136:3000/gameList/' +
              this.type +
              '?isAll=false&userId='
            axios.get(this.apiConfig.list + this.user).then(res => {
              this.gameList = res.data.payload || res.data
            })
          }
        })
    }
  }
}
</script>
